  body {
    min-height: 100vh;
    display: flex;
    font-weight: 400;
    font-family: sans-serif;
    background: #12c2e9;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #3646d6, #1276e9);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, $sky-blue 0%,$sky-dark-blue 100%); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
  }
  
