* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html,
body {
  height: 100%; }

html {
  font-family: Arial, Helvetica, sans-serif;
  background: linear-gradient(to right, #57c1eb 0%, #246fa8 100%);
  font-size: 10px; }

body {
  display: flex;
  justify-content: center; }

#root {
  display: grid;
  place-items: center center; }

::-webkit-scrollbar {
  display: none; }

#search-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-area: search-container;
  background: #0048AA;
  padding: 0 20px;
  border-radius: 10px 0 0 0;
  box-shadow: 0 1px 3px -1px #000000bf;
  z-index: 1; }
  #search-container h1 {
    color: white; }

.Modal {
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  background-color: white;
  width: 40%;
  margin: auto;
  text-align: center;
  padding: 30px 20px; }

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(165, 161, 161, 0.5);
  z-index: 9999; }

.container {
  margin: 30px auto;
  padding: 20px; }

h1 {
  font-size: 20px;
  text-align: center;
  margin: 20px 0 20px; }
  h1 small {
    display: block;
    font-size: 15px;
    padding-top: 8px;
    color: gray; }

.g-name {
  width: 60%;
  margin-top: 25px;
  border: 0;
  border-bottom: 2px solid blue;
  padding: 10px 0px 10px 5px;
  font-size: 14px;
  font-style: italic;
  margin-bottom: 25px; }

.avatar-upload {
  position: relative;
  max-width: 205px;
  margin: 20px auto; }
  .avatar-upload .avatar-edit {
    position: absolute;
    right: 12px;
    z-index: 1;
    top: 10px; }
    .avatar-upload .avatar-edit input {
      display: none; }
      .avatar-upload .avatar-edit input + label {
        display: inline-block;
        width: 34px;
        height: 34px;
        margin-bottom: 0;
        border-radius: 100%;
        background: #FFFFFF;
        border: 1px solid transparent;
        box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
        cursor: pointer;
        font-weight: normal;
        transition: all .2s ease-in-out; }
        .avatar-upload .avatar-edit input + label:hover {
          background: #f1f1f1;
          border-color: #d6d6d6; }
  .avatar-upload .avatar-preview {
    width: 192px;
    height: 192px;
    position: relative;
    border-radius: 100%;
    border: 6px solid #F8F8F8;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1); }
    .avatar-upload .avatar-preview > div {
      width: 100%;
      height: 100%;
      border-radius: 100%;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center; }

#new-message-container {
  display: grid;
  grid: 35px / 1fr 40px;
  align-content: center;
  align-items: center;
  grid-area: new-message-container;
  background: #0048AA;
  border-radius: 0 0 0 10px;
  padding: 15px; }
  #new-message-container div h2 {
    color: white;
    text-transform: capitalize; }
  #new-message-container .c-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #EEE;
    border-radius: 100%;
    color: #003781;
    text-decoration: none;
    font-size: 3.6rem;
    line-height: 3.6rem;
    outline: none;
    border: none;
    cursor: pointer; }
  #new-message-container .c-logout {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #EEE;
    color: #003781;
    text-decoration: none;
    font-size: 1.5rem;
    outline: none;
    border: none;
    cursor: pointer;
    align-self: center;
    margin: 5px 0px 0px 21px; }

.primary-button {
  flex: 0 0 auto;
  background: #0048AA;
  border: 1px solid #003781;
  color: white;
  padding: 12px;
  border-radius: 5px;
  font-size: 1.4rem;
  cursor: pointer;
  outline: none; }
  .primary-button:disabled {
    background: #778eaf;
    border: 1px solid #747d8f; }

#no-coversation-layout {
  flex: 1 0;
  display: flex;
  flex-direction: column; }
  #no-coversation-layout #no-conversation-content {
    flex: 1 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0 0 10px 10px; }
  #no-coversation-layout h2 {
    color: #0048AA;
    font-size: 3.5rem;
    margin-bottom: 2rem; }
  #no-coversation-layout p {
    font-size: 2rem; }
  #no-coversation-layout p:last-of-type {
    margin-bottom: 1.5rem; }

.conversation {
  display: grid;
  grid-template-columns: 40px 1fr -webkit-max-content;
  grid-template-columns: 40px 1fr max-content;
  grid-gap: 10px;
  color: #FFF;
  font-size: 1.3rem;
  border-bottom: 1px solid #003781;
  padding: 20px 20px 20px 15px; }
  .conversation.active, .conversation:hover {
    background: #003781; }
  .conversation:hover {
    cursor: pointer; }
  .conversation > img {
    grid-row: span 2;
    height: 50px;
    width: 50px;
    border-radius: 100%; }
  .conversation .title-text {
    font-weight: bold;
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis; }
  .conversation .created-date {
    color: #DDD;
    white-space: nowrap;
    font-size: 1rem; }
  .conversation .conversation-message {
    grid-column: span 2;
    padding-left: 5px;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis; }

#conversation-list {
  grid-area: conversation-list;
  background: #0048AA;
  overflow-y: scroll; }

.trash-logo {
  stroke: #0048AA;
  cursor: pointer; }

.add-user {
  stroke: #0048AA;
  cursor: pointer;
  margin-top: 5px; }

#chat-title {
  display: grid;
  grid: 36px / 1fr 26px 15px 36px;
  align-content: center;
  align-items: center;
  grid-area: chat-title;
  background: #EEE;
  color: #0048AA;
  font-weight: bold;
  font-size: 2.0rem;
  border-radius: 0 10px 0 0;
  box-shadow: 0 1px 3px -1px #2e2e2e;
  padding: 0 20px;
  z-index: 1; }
  #chat-title img {
    cursor: pointer; }

.modall {
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  background-color: white;
  width: 30%;
  margin: auto;
  text-align: center;
  padding: 35px 40px;
  border: 0px; }

.overlayy {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(165, 161, 161, 0.5);
  z-index: 9999; }

.heading {
  color: #0048AA;
  border-bottom: 1px solid #0048AA;
  padding-bottom: 10px;
  font-style: italic;
  text-align: left;
  margin-bottom: 10px; }

#chat-message-list {
  grid-area: chat-message-list;
  display: flex;
  flex-direction: column-reverse;
  padding: 0 20px;
  overflow-y: scroll; }

.message-row {
  display: grid;
  grid-template-columns: 70%;
  margin-bottom: 20px; }
  .message-row > .message-content {
    display: grid; }
    .message-row > .message-content > img {
      border-radius: 100%;
      grid-row: span 2;
      width: 48px;
      height: 48px; }
    .message-row > .message-content > .message-time {
      font-size: 1.3rem;
      color: #777777; }
    .message-row > .message-content > .message-text {
      padding: 9px 14px;
      font-size: 1.6rem;
      margin-bottom: 5px; }
  .message-row.you-message {
    justify-content: end; }
    .message-row.you-message > .message-content {
      justify-items: end; }
      .message-row.you-message > .message-content > .message-text {
        background: #0048AA;
        color: #FFF;
        border: 1px solid #0048AA;
        border-radius: 14px 14px 0 14px; }
  .message-row.other-message {
    justify-items: start; }
    .message-row.other-message > .message-content {
      grid-column-gap: 15px; }
      .message-row.other-message > .message-content > .message-text {
        background: #EEE;
        color: #111;
        border: 1px solid #cccccc;
        border-radius: 14px 14px 14px 0; }

.attachment-logo {
  fill: #0048AA;
  cursor: pointer;
  width: 32px;
  height: 32px;
  enable-background: new 0 0 512.001 512.001; }

#chat-form {
  display: flex;
  align-items: center;
  grid-area: chat-form;
  background: #EEE;
  border-radius: 0 0 10px 0;
  border-top: 1px solid #cfcfcf;
  padding-left: 42px;
  padding-right: 22px; }
  #chat-form input {
    flex: 1 0;
    outline: none;
    padding: 15px;
    border: 2px solid #dddddd;
    border-right: none;
    color: #333300;
    border-radius: 6px 0 0 6px;
    font-size: 1.4rem;
    margin-left: 15px; }
  #chat-form button {
    height: 51px;
    flex: 0 0 90px;
    border-radius: 0 6px 6px 0; }

#chat-container {
  display: grid;
  grid: 'search-container chat-title' 71px 'conversation-list chat-message-list' 1fr 'new-message-container chat-form' 78px / 275px 1fr;
  min-width: 1000px;
  max-width: 1000px;
  max-height: 800px;
  width: 100%;
  height: 95vh;
  background: white;
  border-radius: 10px; }

body {
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: sans-serif;
  background: #12c2e9;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #57c1eb 0%, #246fa8 100%);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

.outer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left; }

.inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s; }

.outer .form-control:focus {
  border-color: #167bff;
  box-shadow: none; }

.outer h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px; }

.custom-control-label {
  font-weight: 400; }

.forgot-password {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0; }

.signup {
  color: #167bff !important; }

